<template>
    <div class="content">
        <div class="box">
            <div class="top" :style="{backgroundImage:`url(${img})`}">
                <div class="alias">{{storeName}}</div>
                <div class="title">邀请您成为门店合伙人，一起开启海尔新零售！</div>
            </div>
            <div class="bottom">
                <div class="store-message">
                    <div><span>店铺名称：</span>{{storeName}}</div>
                    <div><span>店铺地址：</span>{{storeAddress}}</div>
                </div>
                <div class="hint">
                    <img src="data:image/png;base64," alt="">
                    确认邀请后，您将成为该店铺合伙人，权益将按照店铺来！
                </div>
                <div class="btn" @click="submit" :style="{color:state?'#fff':'#999999',background:state?'#2979FF':'#E4E4E4'}">{{btnContent}}</div>
            </div>
        </div>
    </div>
</template>
<script>
import Api from '@/http/service/invite.js';
export default {
  data() {
    return {
      img: require('../../assets/imgs/invite/addBg.png'),
      storeName: '',
      storeAddress: '',
      btnContent: '确认邀请',
      state: true,
      shareId: null,
      memberId: null,
      partnerLabel: null,
    }
  },
  created() {
    this.initialize()
  },
  methods: {
    /**
   * @description  初始化
   */
    initialize() {
      if (this.$route.query.shareId && this.$route.query.memberId && this.$route.query.partnerLabel) {
        this.memberId = this.$route.query.memberId
        this.shareId = this.$route.query.shareId
        this.partnerLabel = this.$route.query.partnerLabel
        this.open()
      }
    },
    /**
   * @description  打开链接
   */
    open() {
      const data = {
        memberId: this.memberId,
        shareId: this.shareId,
      }
      Api.openInvitation(data).then(res => {
        if (res.data.success) {
          this.storeName = res.data.data.storeName
          this.storeAddress = res.data.data.detailedAddress
        } else {
          this.$toast(res.data.message)
        }
        if (res.data.errorCode === '101' || res.data.errorCode === -100) {
          setTimeout(() => {
            this.$router.replace('/login')
          }, 2000)
        }
        if (res.data.errorCode === '108') {
          this.state = false
          this.btnContent = '已接受邀请'
          this.$toast(res.data.message)
        }
        if (res.data.errorCode === '109') {
          this.$toast(res.data.message)
          this.state = false
        }
      })
    },
    /**
   * @description  确认邀请
   */
    submit() {
      if (this.state) {
        const data = {
          memberId: this.memberId,
          shareId: this.shareId,
          partnerLabel: this.partnerLabel,
        }
        Api.acceptInvitationAdd(data).then(res => {
          if (!res.data.success) {
            this.$toast(res.data.message)
          } else {
            this.state = false
            this.btnContent = '已接受邀请'
          }
        })
      }
    },
  },
}
</script>
